html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

.formulario {
  display: flex;
  flex-direction: column;
  padding: 3rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 2.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem 0 0 0.5rem;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__select, .formulario__textarea {
  padding: 1rem;
  resize: none;
  border: 1px solid rgb(57.3221757322, 66.4937238494, 79.6778242678);
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file, .formulario__textarea--file {
  border: none;
}
.formulario__input--social, .formulario__select--social, .formulario__textarea--social {
  border-radius: 0 0.5rem 0.5rem 0;
  width: 100%;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: flex-start;
}
.formulario__submit:hover {
  background-color: rgb(0, 98.8729508197, 193);
  cursor: pointer;
}
.formulario__fieldset {
  border: 0;
  padding: 0;
  margin-top: 2rem;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  display: block;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #007df4;
  color: #FFFFFF;
  padding: 1rem;
  border-radius: 0.4rem;
  font-size: 1.4rem;
  cursor: pointer;
  font-weight: 700;
  transition: ease 0.3s;
}
.formulario__listado > li:hover {
  background-color: rgb(0, 98.8729508197, 193);
}
.formulario__imagen {
  width: 100%;
  margin: 0 auto;
  max-width: 40rem;
  display: block;
}

.acciones {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.6rem;
  font-weight: 300;
}

.horas {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .horas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horas__hora {
  color: #007df4;
  border: 1px solid #007df4;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
  transition: ease 0.3s;
}
.horas__hora:hover {
  transform: scale(0.9);
}
.horas__hora--seleccionada {
  background-color: #007df4;
  color: #FFFFFF;
}
.horas__hora--deshabilitada {
  border: 1px solid #64748B;
  color: #64748B;
  cursor: not-allowed;
  opacity: 0.5;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.3)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, 100%, cover;
  background-position: top left, right bottom, 0, center;
}
.header__contenedor {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlace {
  color: #FFFFFF;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 700;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__enlace:hover {
  color: #007df4;
}
.header__contenido {
  padding-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__contenido {
    padding-top: 5rem;
    align-items: flex-start;
  }
}
.header__logo {
  font-size: clamp(3rem, 10vw, 8rem);
  background-image: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  display: inline-block;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
  }
}
.header__texto {
  font-size: clamp(2rem, 2vw, 3rem);
  font-weight: 700;
  color: #FFFFFF;
  margin: 1rem 0;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__texto {
    text-align: left;
  }
}
.header__texto--modalidad {
  font-size: clamp(1.6rem, 2vw, 2rem);
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  margin: 2rem 0;
}
.header__boton:hover {
  background-color: rgb(0, 149, 144.53);
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}

.barra {
  background-color: #007df4;
}
.barra__contenido {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, 5vw, 3rem);
}
@media (min-width: 1024px) {
  .barra__logo {
    text-align: left;
    margin: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 2rem;
  transition: ease 0.3s;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  display: grid;
  place-content: center;
}
.navegacion__enlace--actual, .navegacion__enlace:hover {
  background-color: #FFFFFF;
  color: #007df4;
}

.footer {
  background-color: #007df4;
  margin-top: 5rem;
  padding-top: 5rem;
  color: #FFFFFF;
}
.footer__grid {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, 6vw, 4rem);
}
@media (min-width: 1024px) {
  .footer__logo {
    text-align: left;
    margin: 0;
  }
}
.footer__texto {
  text-align: center;
  line-height: 2;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: rgb(0, 98.8729508197, 193);
  margin: 0;
  margin-top: 3rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 900;
  padding: 2rem 0;
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 4rem 0;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  margin-left: 0.4rem;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.3rem;
  font-weight: 700;
  letter-spacing: 0.2rem;
}
.alerta--error {
  background-color: #ffdcdc;
  border-left: 5px solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta--exito {
  background-color: rgb(19.1538461538, 252.8461538462, 19.1538461538);
  border-left: 5px solid rgb(0.6153846154, 67.3846153846, 0.6153846154);
  color: rgb(0.6153846154, 67.3846153846, 0.6153846154);
}

.auth {
  overflow: hidden;
  width: min(95%, 70rem);
  margin: 0 auto;
}
.auth__texto {
  text-align: center;
}

.devwebcamp__grid {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .devwebcamp__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.devwebcamp__texto {
  line-height: 2;
}

.paquetes__grid {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .paquetes__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.paquetes__submit {
  margin-top: 3rem;
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.paquetes__submit:hover {
  background-color: rgb(0, 98.8729508197, 193);
  cursor: pointer;
}

.paquete {
  border: 1px solid rgb(127.050209205, 141.7782426778, 162.949790795);
  padding: 3rem;
  display: flex;
  flex-direction: column;
}
.paquete__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.paquete--premium {
  border: 2px solid #ac7301;
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .paquete--premium {
    grid-row: auto;
  }
}
.paquete__lista {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 2;
}
.paquete__precio {
  font-size: 6rem;
  margin: 3rem 0 0 0;
  color: #007df4;
  text-align: center;
  font-weight: 900;
  flex: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.agenda {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .agenda {
    padding: 5rem;
  }
}

.eventos__heading {
  color: #007df4;
  margin: 5rem 0;
  text-align: left;
  font-size: clamp(2rem, 8vw, 6rem);
}
.eventos__fecha {
  color: #64748B;
  margin: 3rem 0 1rem 0;
}
.eventos--workshops .eventos__heading {
  color: rgb(0, 149, 144.53);
}
.eventos--workshops .evento__informacion {
  background-color: rgb(0, 149, 144.53);
}
.eventos--workshops .evento__informacion:hover {
  background-color: rgb(0, 98, 95.06);
}
.eventos__animation-wrapper {
  overflow: hidden;
}
.eventos .swiper-button-next,
.eventos .swiper-button-prev {
  color: rgba(26, 27, 21, 0.5);
}

.evento__nombre {
  font-size: 2rem;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 0;
}
.evento__hora {
  font-weight: 700;
}
.evento__informacion {
  padding: 2rem;
  border-radius: 1rem;
  background-color: #007df4;
  color: #FFFFFF;
  height: 18rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.evento__informacion:hover {
  background-color: rgb(0, 98.8729508197, 193);
}
.evento__descripcion {
  color: #FFFFFF;
  font-size: 1.6rem;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-align: left;
  overflow: hidden;
  margin: 0;
}
.evento__ponente {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
.evento__imagen-autor {
  width: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.evento__ponente-nombre {
  font-weight: 700;
}

.resumen {
  padding: 12rem 0;
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), image-set(url(../img/bg_numeros.avif) 1x, url(../img/bg_numeros.webp) 1x, url(../img/bg_numeros.jpg) 1x);
  background-size: cover;
  background-position: center;
}
.resumen__grid {
  overflow: hidden;
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .resumen__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resumen__texto {
  text-align: center;
  font-weight: 900;
  line-height: 1;
  font-size: 2.4rem;
  text-transform: uppercase;
  color: #FFFFFF;
